import { useMemo, useEffect } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { getCustomerDetails, setCurrentCustomerProfileCompletion } from "../redux/customerSlice";

import { RingProgress, Text, Box, Loader } from "@mantine/core";
import StyledTable from "../StyledComponents/StyledTable";

export default function TableDisplay({
  customerList,
  fetchedPofileCompleteness,
  topProps,
}) {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const customerDetailsStatus = useSelector(state => state.customer.customerDetailsStatus)


  const profileCompletion = (percentage) => {
    return (
      <Box>
        <RingProgress
          size={45}
          thickness={3}
          sections={[
            {
              value: percentage,
              color:
                percentage > 25
                  ? "#1D9B25"
                  : percentage > 50
                    ? "#CFA400"
                    : "#D85972",
            },
          ]}
          label={
            <Text color="" weight={20} align="center" size="xs">
              {percentage}%
            </Text>
          }
        />
      </Box>
    );
  };

  const handleRowClick = (customer) => {
    console.log(customer);
    dispatch(getCustomerDetails(customer.id));
    dispatch(setCurrentCustomerProfileCompletion(customer.profile_completion))
  };


  useEffect(() => {
    if (customerDetailsStatus === "success") {
      navigate("/dashboard");
    }
  }, [customerDetailsStatus, navigate]);

  const columns = useMemo(
    () => [
      {
        accessorFn: (row) => `${row.profiling?.personal_details?.full_name}`,
        id: "name",
        header: "Name",
      },
      {
        accessorFn: (row) => `CLID${row.id.substr(0, 6)}....`,
        id: "customerId",
        header: "Customer Id",
      },
      {
        accessorKey: "email",
        header: "Email",
        minSize: 400,
      },
      {
        accessorKey: "source",
        header: "Source",
      },
      {
        accessorKey: "mobile",
        header: "Mobile",
      },
      {
        accessorFn: (row) =>
          !fetchedPofileCompleteness ? (
            <Loader type={"dots"} color="#0d5ff9" />
          ) : (
            row.profile_completion
          ),
        id: "profileCompletion",
        header: "Profile Completion(in %)",
      },
    ],
    [fetchedPofileCompleteness],
  );

  return (
    <Box>
      <StyledTable
        columns={columns}
        data={customerList}
        onRowClick={handleRowClick}
        topProps={topProps}
      />
    </Box>
  );
}
